
import { defineComponent, onMounted, Ref, ref } from "vue";
export default defineComponent({
  name: "LoaderImage",
  props: {
    source: { type: String, required: true },
    alt: { type: String, default: null },
  },
  components: {},
  setup() {
    const loading = ref(true);
    function onLoad() {
      loading.value = false;
    }

    return {
      onLoad,
      loading,
    };
  },
});
